import { NextResponse } from 'next/server';

// 缓存数据和过期时间
let cachedStats: any = null;
let cacheExpiry: number = 0;
const CACHE_DURATION = 60 * 1000; // 1分钟缓存

// 获取仪表盘统计数据
export async function GET() {
  try {
    // 检查缓存是否有效
    const now = Date.now();
    if (cachedStats && now < cacheExpiry) {
      return NextResponse.json(cachedStats, { status: 200 });
    }

    // 注意：当前Prisma客户端配置存在问题，无法连接到数据库
    // 以下使用模拟数据，待Prisma客户端配置正确后，可替换为实际数据库查询
    const stats = {
      totalPosts: 15,
      totalUsers: 5,
      totalComments: 23,
      totalCategories: 6,
      lastUpdated: new Date().toISOString(),
      dataSource: 'mock' // 明确标记数据来源为模拟数据
    };

    // 更新缓存
    cachedStats = stats;
    cacheExpiry = now + CACHE_DURATION;

    return NextResponse.json(stats, { status: 200 });
  } catch (error) {
    console.error('获取仪表盘统计数据失败:', error);
    
    // 返回模拟数据作为最后的后备
    const fallbackStats = {
      totalPosts: 10,
      totalUsers: 2,
      totalComments: 5,
      totalCategories: 3,
      lastUpdated: new Date().toISOString(),
      dataSource: 'fallback'
    };
    
    return NextResponse.json(fallbackStats, { status: 200 });
  }
}